import React, { useEffect, useState } from 'react';
import CSGOGSI, { CSGO } from 'csgogsi-socket';
import './App.css';
import LexoRadarContainer from './Radar/LexoRadar/LexoRadarContainer';
declare var window: any;

const { GSI } = CSGOGSI(`http://${window.ip}:${window.port}`, "update");


function App() {
  const [ gsi, setGSI] = useState<CSGO | null>(null);
  useEffect(() => {
    GSI.on("data", data => {
      setGSI(data);
    });
  }, []);
  if(!gsi) return null;
  
  return (
    <div className="custom-radar-external">
      <LexoRadarContainer
        player={gsi.player}
        players={gsi.players}
        bomb={gsi.bomb}
        grenades={gsi.grenades}
        size={350}
        mapName={gsi.map.name.substr(gsi.map.name.lastIndexOf('/')+1)}
      />
    </div>
  );
}

export default App;
